import APICONFIG from "./APICONFIG"


export default async function CallAPI(method,url){
    try{
        let config={
            url,
            method,
            headers:{
                'Access-Control-Allow-Origin':'*'
            }
        }
        
        const respond = await APICONFIG(config);
        if(respond) return respond
    }catch(e){
        console.log("API Error => ",e)
    }
}