const Home = () => {
  const [appState, setAppState] = useState({ loading: false, repos: null });
  useEffect(() => {
    setAppState({ loading: true });
    call();
  }, [setAppState]);
 async function call() {
    const apiurl = "https://fajarman.000webhostapp.com/services/api/banner"; //"https://raw.githubusercontent.com/fajardsx/Cfg_Repo/master/cfg_web.json";
    const respond = await CallAPI('GET',apiurl)
    if(respond && respond.status === 200 )setAppState({ loading: false, repos: respond.data });
    console.log("respons ",respond);
    // axios.get(apiurl).then((result) => {
    //   const resultData = result.data;
    //   //console.log("ResultData",resultData)
    //   setAppState({ loading: false, repos: resultData });
    // });
  }
  return (
    <>
      {appState.repos && <MyBanner data={appState.repos.Banner} />}
      <div className="App-link Porto-Center">
        <div className="porto-centerpart">
          <WelcomeContainer />
        </div>
      </div>
      <div className="App-link Porto-Center">
        <div className="porto-top ">
          <div className="TextTitleContainer">
            <div className="TextTitlePorto">MY PORTFOLIO</div>
          </div>
          <br />
          {appState.repos && (
            <PortoContainer key={"apps"} data={appState.repos.Homemobile} title="Mobile Apps" />
          )}

          <br />
          {appState.repos && <PortoContainer key={"game"} data={appState.repos.Homegame} title="Games" />}

          <br />
          <div className=" mb-2 CenterText">
            <a id="MorePorto" href="/portofolio">
              <h3>MORE PORTFOLIO</h3>
            </a>
          </div>
          <br />
        </div>
      </div>

      <div className="App-link Porto-Center">
        <div className="porto-centerbottom TextTitle">
          <div className="TextTitleContainer ">
            <div className="TextTitleAbout">ABOUT ME</div>
          </div>
          <br />
          <AboutDiv data={appState.repos} />
        </div>
      </div>
      <div className="App-link Porto-Center">
        <div className="porto-footer">
          <br />
          <Footer />
        </div>
      </div>
    </>
  );
};
export default Home;
